.fa-heart {
  width: 22px;
  height: 22px;
  color: black;
  font-size: 22px;
  position: relative;
  transition: color 1s ease-in-out;

  &::after {
    content: "\f004";
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    @include gradient-background;
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
    opacity: 0;
    transition: opacity 1s ease-in-out;
  }

  &:hover::after {
    opacity: 1;
  }

  &:hover {
    color: transparent;
  }
}
